*{
    margin: 0;
    padding: 0;
}

.Menustyle {
    width: 100%;
    height: 100px;
    background: green;
    color: white;


}

.Menustyle ul{
    height: 100px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
}

.Menustyle ul li{
    display: flex;
    justify-content: space-around;
    list-style: none;
}